import request from '../../../axios/request/request'
const actions = {
  /**
   * 预受理模块
   */
  // 公用的list接口
  async getRequsetList({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e72e2bfe2c5f3000144803f/findCaseList',
    params);
  },
  // 受理
  async acceptance({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e72e2bfe2c5f3000144803f/acceptPass', params)
  },
  // 批量受理
  async acceptanceAll({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e72e2bfe2c5f3000144803f/accept_batchPass', params)
  },
  // 驳回
  async rejectCase({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e72e2bfe2c5f3000144803f/acceptReject', params)
  },
  // 批量驳回
  async rejectCaseAll({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e72e2bfe2c5f3000144803f/accept_batchReject', params)
  },

  /**
   * 待缴费审核
   */
  //待缴费审核列表
  async pendingPaymentList({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e72e3d9e2c5f30001448040/paymentOrderList', params)
  },
  // 3.4.2.审核通过、驳回、批量通过、批量驳回
  async verifyOneOrAll({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e72e3d9e2c5f30001448040/paymentOrderApprove', params)
  },
  /**
   * 待立案
   */
  // 案件 案由
  async waitCaseTypes({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e4b477315df8d17c429417b/caseType', params)
  },
  // 立案 单个
  async setCaseOne({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e44ca2ecef99317c419cfd1/case_UPCASE_submit', params)
  },
  // 立案 确认 批量
  async setCaseAll({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e44ca2ecef99317c419cfd1/case_submit_batch', params)
  },
  // 驳回 单个
  async setrejectOne({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e44ca2ecef99317c419cfd1/case_UPCASE_REJECT', params)
  },
  // 立案 批量驳回
  async setrejectAll({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e44ca2ecef99317c419cfd1/case_UPCASE_REJECT_batch', params)
  },

  /**
   *  立案审核列表
   */
  async filingCaseOneOrAll({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e748c645f75230001453dc4/upcaseApprovePass', params)
  },
  // 立案驳回
  async filingCaseBack({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e748c645f75230001453dc4/upcaseApproveReject', params)
  },

  /**
   * 立案成功 案件列表
   */
  // 查看文件列表
  async filingCaseSuccessList({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e72e40ce2c5f30001448041/sendCommon', params)
  },
  // 立案送达 手机送达
  async filingCaseSend({ commit }, params) {
    return await request.post('/api-entity/entity/script/action/5e6882f59880160001da31a6/registerService', params)
  },


}
export default actions
