import request from '../../../axios/request/request'
const urlMark = '/api-entity/entity/script/action'
const actions = {
  /**
   * 组庭
   */
  //------------------------文书模版
  async getTemplateList ({commit}, params) {
    return await request.post(urlMark + '/5e7877d4b88dfa0001a43474/queryTemplate', params);
  },
  // // 获取服务端菜单
  // 组庭单个呈报、批量组庭呈报
  async setReport({ commit }, params){
    return await request.post('/api-entity/entity/script/action/5e72e419e2c5f30001448042/actionCommon', params);
  },
  // 组庭审核列表
  async getVerifyList ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e786a165f7523000173319d/court_queryauth', params);
  },
  // 分配仲裁员
  async distributePerson ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e786a165f7523000173319d/court_specify_arb', params);
  },
  // 批量分配仲裁员
  async distributePersonAll ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e786a165f7523000173319d/court_specify_arb_batch', params);
  },
  // 确认仲裁员
  async surePerson ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e786a165f7523000173319d/court_accept', params);
  },


  /**
   * 审理方式
   */
    // 确定转书面审理
  async sureWrittenHear ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e6f25c3f562a50001717e2a/writtenHear', params);
  },
  // 指定开庭时间
  async setCourtTime ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e6f25c3f562a50001717e2a/courtTime', params);
  },
  // 进入庭室
  async enterCourtRoom ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e72e419e2c5f30001448042/findMeetingUrl', params);
  },
  // 获取庭审记录
  async getCourtRecord ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e72e419e2c5f30001448042/getCourtRecord', params);
  },
  // 预约开庭
  async reserveMeeting ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e72e419e2c5f30001448042/reserveMeeting', params);
  },
  // 上传问题清单
  async uploadReason ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e4b836615df8d36f42e90f3/lian_add_evidence', params);
  },
  // 关闭庭室
  async closeMeetingRoom ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e72e419e2c5f30001448042/closeMeeting', params);
  },
  // 记录
  async recordCount ({commit}, params) {
    return await request.post('/api-entity/entity/script/action/5e72e419e2c5f30001448042/saveCourtRecord', params);
  },

}
export default actions
