// vue filter 
// author zcy

// 数字 不足10 补0 如：9 -> 09
function singleFormat (str) {
  return str.toString().length === 1 ? ('0' + str) : str
}
// 年月日  time 时间戳 mark 连接符号
function getYMD (time, symbol) {
  if(!symbol) {
    symbol = '-'
  }
  let year = time.getFullYear()
  let month = time.getMonth() + 1
  let date = time.getDate()
  return year + symbol + singleFormat(month) + symbol + singleFormat(date)
}
// 时分秒
function getHMS (time) {
  let hours = time.getHours()
  let minutes = time.getMinutes()
  let seconds = time.getSeconds()
  return singleFormat(hours) + ':' + singleFormat(minutes) + ':' + singleFormat(seconds)
}

const Filters =  {
  // 三位加逗号
  numberFormat (num) {
    if (num) {
      let [num1, num2] = num.toString().split('.')
      num1 = num1.split('').reverse().map((item, index) => {
        return (index) % 3 || !index || item === '-' || item === '+' ? item : item + ','
      }).reverse().join('')
      num2 ? num = num1 + '.' + num2 : num = num1
    }
    return num
  },
  /**
   * 时间格式化
   * @param {*} time  时间戳
   * @param {*} type 默认 2020-01-01 10:10:10 
   * type = 'YMD' 2020-01-01
   * tyee = 'HMS' 10:10:10
   * @param {*} symbol 
   */
  timeFormat (time, type, symbol) {
    let GTM_8 = 480  // -480
    let date = new Date(time)
    let subMin = date.getTimezoneOffset() + GTM_8
    date.setMinutes(date.getMinutes() + subMin)
    if (!type) return getYMD(date, symbol) + ' ' + getHMS(date)
    switch (type) {
      case 'HMS': return getHMS(date)
      case 'YMD': return getYMD(date, symbol)
    }
  }
}
export default Filters