import request from '../../../axios/request/request'
const urlMark = '/api-entity/entity/script/action'
function checkReturnData(res) {
  if (res.data.flag) {
    return res.data
  } else {
    return Promise.reject(res.data)
  }
}
const state = {

}

const actions = {
  /**
   * 获取身份（个人/企业）审核--列表123123
   */
  async getVerifyList ({commit},params) {
    let res = await request.post(urlMark + '/5e65e934a2bf9e0001b05e03/mine', params)
    return checkReturnData(res)
     
  },
  /**
   * 获取身份（个人/企业）审核--详情
   */
  async getVerifyDetail ({commit}, params) {
    let res =  await request.post(urlMark + '/5e65e934a2bf9e0001b05e03/details', params)
  },
  /**
   * 获取身份（个人/企业）审核--操作
   */
  async verifyOperate ({commit}, params) {
    let res =  await request.post(urlMark + '/5e65e934a2bf9e0001b05e03/updateRegisterPersonal', params)
    return checkReturnData(res)
  },
}
const verfication = {
  namespaced: true,
  state,
  actions
}
export default verfication