import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routers'
import { LoadingBar } from 'iview'
import { setTitle, resetTokenAndClearUser } from '@/libs/util'
import store from '../store'
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})
// 动态添加路由 设置权限
router.$addRoutes = (params) => {

  router.matcher = new VueRouter({ mode: 'hash' }).matcher;
  router.addRoutes(params)
}
// 是否有菜单数据
let hasMenus = false
// 跳转前的逻辑判断
router.beforeEach((to, from, next) => {
  let token = sessionStorage.getItem('token')
  // console.log('去哪：',to, from )
  if (token) {
    if (to.path === '/login') {
      next({ path: '/' })
    } else if (hasMenus) {
      next()
    } else {
      try {
        // 动态添加路由
        router.$addRoutes(store.getters.addRouters)
        hasMenus = true
        if(to.meta.hideInMenu){
          // 进入404 或 返回登录页
          resetTokenAndClearUser()
          next({ path: '/login' })
        } else {
          next({ path: to.path || '/' })
        }
        
      } catch (error) {
        resetTokenAndClearUser()
        next({ path: '/login' })
      }
    }
  } else {
    hasMenus = false
    if (to.path === '/login') {
      next()
    } else {
      next({ path: '/login' })
    }
  }

})

router.afterEach(to => {
  setTitle(to, router.common)
  LoadingBar.finish()
  window.scrollTo(0, 0)
})

export default router
