import routers from '@/router/routers'

// 设置动态路由
function setActiveRouter(array, routers) {
  let arr = JSON.parse(JSON.stringify(array))
  if (!arr || arr.length == 0) {
    return []
  }
  var level = {};
  for (var i = 0; i < arr.length; i++) {

    level[arr[i].url] = arr[i].hidden
    if (arr[i].subMenus && arr[i].subMenus.length > 0) {

      for (var j = 0; j < arr[i].subMenus.length; j++) {
        level[arr[i].subMenus[j].url] = arr[i].subMenus[j].hidden
      }
    }
  }
  // console.log(Object.keys(level))
  for (var i = 0; i < routers.length; i++) {
    let path =  ''
    if(routers[i].path){
      path=routers[i].path.replace('/', '')
    }
    if (Object.keys(level).includes(path)) {
      // console.log(routers[i].path) // 需要动态配置的路由 一级
      routers[i].meta.hideInMenu = level[path]
    }

    // 二级
    if (routers[i].children && routers[i].children.length > 0) {
      setActiveRouter(arr, routers[i].children)
    }
  }
  
  return routers
}
const getters = {
  addRouters:(state)=>{
    return setActiveRouter(state.menuList,routers)
    }
  }
  export default getters
  