import request from '@/axios/request/request'
const urlMark = '/api-entity/entity/script/action'
// 返回数据统一处理

const actions = {
  /**
   * 根据token 获取用户信息
   */
  async getUserInfo () {
    return await request.get('api-user/users/current')
  },
  /**
   * 修改用户信息
   */
  async updateUser ({commit}, params) {
    return await request.post('api-user/users/user', params, {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json; charset=UTF-8'
      }
    })
  },
  /**
   * 修改用户密码
   */
  async updatePassword ({commit}, params) {
    return await request.put('api-user/users/password', params, {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json; charset=UTF-8'
      }
    })
  }
}

const personCenter = {
  namespaced: true,
  actions
}
export default personCenter