/**
 * coder：zcy
 * 注意事项：：：
 * 1、meta 中参数的作用
 *  title 
 *  hideInMenu  true: 隐藏  false: 显示
 * 2、name 属性写准确点 跳转路由根据name跳转 
 * 所以即便是不同父级下的name也尽量不要相同
 *  
 */
