import Vue from 'vue'
import Vuex from 'vuex'


import state from './state'
import mutations from './mutation'
import getters from './getters'
import actions from './actions'
import createLogger from 'vuex/dist/logger'
// module

import  createCase from './module/createCase' // 立案
import replyPeriod from './module/replyPeriod' // 案件分配
import  organizePeriod from './module/organizePeriod' // 组庭
// 身份审核
import verification from './module/verification'
// 个人中心
import personCenter from './module/personCenter'
Vue.use(Vuex)
const debug = process.env.NODE_ENV !== 'production';
export default new Vuex.Store({
  modules: {
    organizePeriod,
    personCenter,
    verification,
    createCase,
    replyPeriod
  },
  state,
  actions,
  mutations,
  getters,
  strict: debug,
  plugins: debug ? [createLogger()] : []
 
})
