import request from '../axios/request/request'
import {setToken} from '@/libs/util'
import { Message } from 'iview';

const setMenu=(arr)=> {
  var menu = [];
  if(!arr){
    return []
  }
  for (let i = 0; i < arr.length; i++) {
    var obj = {}
    obj['icon'] = arr[i].css;
    obj['name'] = arr[i].url;
    obj['meta'] = {
      title: arr[i].name,
      hideInMenu: arr[i].hidden
    }
    // console.log(arr[i])
    if (arr[i].subMenus && arr[i].subMenus.length > 0) {
      obj['children'] = []
      for (var j = 0; j < arr[i].subMenus.length; j++) {
        var objchild = {}
        objchild['icon']=arr[i].subMenus[j].css;
					objchild['name']=arr[i].subMenus[j].url;
					objchild['meta'] = {
            icon: arr[i].subMenus[j].css,
          title: arr[i].subMenus[j].name,
          hideInMenu: arr[i].subMenus[j].hidden
        }
        obj['children'].push(objchild)
      }
    }

    menu.push(obj)
  }
  return menu
}
const actions = {
  async userLogin({ commit }, params) {
    try {
      let res = await request.post('/api-uaa/oauth/user/token', params, {
        headers: {
          'Authorization': 'Basic ' + window.btoa("webApp:webApp")
        }
      });
      
      if (res.data.resp_code == 0) {
        setToken(res.data.datas.access_token)
        commit('SET_TOKEN', res.data.datas.access_token);
        Message.success({
          content: '欢迎您登录成功！',
          duration: 3
        })
      } else {
        Message.error({
          content:res.data.resp_msg,
          duration:3
      })
      }
      return { code: 0, data: res.data, errMsg: "" }

    } catch (e) {
      console.error(e)
      return { code: 1, data: null, errMsg: e }
    }
  },
  // 获取服务端菜单
  async getUserMenu({ commit }, params){
    try {
      const res =  await request.get('/api-user/users/menu/current',params)
      
      if(res.data){
        
        let menu= setMenu(res.data)
        commit('serverMenuList', menu)
        return { code: 0, data: res, errMsg: "" }
      }
    } catch (error) {
      console.error(error)
      return { code: 1, data: null, errMsg: error }
    }
  },

  outLogin({ commit }) {
    console.log('clear');
    sessionStorage.clear()
    localStorage.clear();
    commit("clearAll", '')
    return 0
  },
  async downloadReq({ commit }, params) {
    try {
      const res = await request.post(params.url, params.params, { responseType: 'blob' });
      return res.data
    } catch (e) {
      console.error(e);
      return { code: 1, errMsg: '操作失败' }
    }
  },
}
export default actions
