import axios from 'axios'
import qs from 'qs'
import { api } from '../api'
import store from '../../store'
import router from '../../router'
import config from './config'
import { Message } from 'iview'
let pending = []
axios.defaults.withCredentials = true // 设置cookie
axios.defaults.crossDomain = true
axios.defaults.retry = 2
axios.defaults.retryDelay = 500
const cancelToken = axios.CancelToken
const service = axios.create(config)
// 请求拦截器
service.interceptors.request.use((config) => {
    //   removePending(config);
    config.headers = Object.assign(config.method === 'get' ? {
        'Accept': 'application/json',
        'Content-Type': 'application/json; charset=UTF-8'
    } : {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        }, config.headers);

    if (config.method === 'post') {
        const contentType = config.headers['Content-Type'];
        // 根据Content-Type转换data格式
        if (contentType) {
            if (contentType.includes('multipart')) { // 类型 'multipart/form-data;'
                // config.data = data;
            } else if (contentType.includes('json')) { // 类型 'application/json;'
                // 服务器收到的raw body(原始数据) "{name:"nowThen",age:"18"}"（普通字符串）
                config.data = JSON.stringify(config.data);
            } else { // 类型 'application/x-www-form-urlencoded;'
                // 服务器收到的raw body(原始数据) name=nowThen&age=18
                config.data = qs.stringify(config.data);
            }
        }
    }
    if (config.method == 'put') {
        config.data = JSON.stringify(config.data);
    }
    /* eslint-disable */
    if (config.method === 'post') {
        if (!config.url.includes('login') && !config.url.includes('mobile') && !config.url.includes('code/image') && !config.url.includes('token') && !config.url.includes('checkingPhone') && !config.url.includes('addUnUserInClient')) {
            config.headers.authorization = 'Bearer '+ sessionStorage.getItem('token')
        }
    }
    if (config.method === 'get') {
        config.headers.authorization ='Bearer '+ sessionStorage.getItem('token')
    }
    config.cancelToken = new cancelToken((c) => {
        pending.push({ url: config.url + '&request_type=' + config.method, cancel: c })
    })
    return Promise.resolve(config);
}, (error) => {
    return Promise.reject(error)
})
// 返回拦截器
service.interceptors.response.use((response) => {
    return Promise.resolve(response);

}, (error) => {
    console.log(error.response, 'error')
    if (error.response) {
        if(error.response.status==401) {
            // 401: 未登录                
            // 未登录则跳转登录页面，并携带当前页面的路径                
            // 在登录成功后返回当前页面，这一步需要在登录页操作。
            router.push({path: '/login'});
        } else if(error.response.status==403) {
            Message.error({
                content:'登录过期，请重新登录',
                duration:3
            })
                // 清除token                    
                localStorage.removeItem('token');
                // store.commit('loginSuccess', null);
                // 跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面
                setTimeout(() => {
                    router.push('/login')
                }, 1000);
        }
        // switch (error.response.status) {
        //     // 401: 未登录                
        //     // 未登录则跳转登录页面，并携带当前页面的路径                
        //     // 在登录成功后返回当前页面，这一步需要在登录页操作。                
        //     case 401:
        //         router.push({path: '/login'});
        //         break;
        //     // 403 token过期                
        //     // 登录过期对用户进行提示                
        //     // 清除本地token和清空vuex中token对象                
        //     // 跳转登录页面                
        //     case 403:
            
        //     Message.error({
        //         content:'登录过期，请重新登录',
        //         duration:3000
        //     })
        //         // 清除token                    
        //         localStorage.removeItem('token');
        //         // store.commit('loginSuccess', null);
        //         // 跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面
        //         setTimeout(() => {
        //             router.push('/login')
        //         }, 1000);
        //         break;
        //     // 404请求不存在                
        //     case 404:
        //         Message.error({
        //             content:'网络请求不存在',
        //             duration:3000
        //         })
        //         break;
        //     // 其他错误，直接抛出错误提示                
        //     default:
        //         Message.error({
        //             content:error.response.data.message,
        //             duration:3000
        //         })
        // }

    }
    return Promise.reject(error.response);

})


export default service