<template>
  <div class="popup_box">
    <Modal v-model="popShow" :width="width">
      <p slot="header">
        <span>{{titleText}}</span>
      </p>
      <slot></slot>
      <div slot="footer">
        <Button v-if="footerBtn" type="primary" @click="confirm">确定</Button>
        <Button v-if="footerBtn" @click="cancel">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'popup_box',
  props: {
    titleText: {
      type: String,
      default: '操作'
    },
    show: {
      type: Boolean,
      default: false
    },
    footerBtn: {
      type: Boolean,
      default: true
    },
    width: {
      type: String,
      default: '600'
    }
  },
  data () {
    return {
      // popShow : this.show
    }
  },
  computed: {
    popShow: {
      get () {
        return this.show
      },
      set (val) {
        this.$emit('popupCancel', val)
        return val
      }
    }
  },
  mounted () {
  },
  methods: {
    // 确定
    confirm () {
      this.$emit('popupConfirm')
    },
    cancel () {
      this.$emit('popupCancel')
    }
  }
}
</script>
<style lang="less">
.ivu-modal-content {
  background: #EFF6FF;
}
.ivu-modal-header {
  // background: #EFF6FF;
  box-shadow:0px 3px 3px 0 rgba(226,238,252,1);
  border-radius:10px;
}
.ivu-modal-footer div, .ivu-modal-footer{
  text-align: center;
  border: none;
}
.popup_content {
  text-align: center;
  font-size:18px;
  font-family:PingFangSC-Semibold,PingFang SC;
  font-weight:600;  
  color: #3B4453;
}
</style>
